* {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

html {
    width: 100%;
    height: 100%;
}

body {
    width: 100%;
    height: 100%;
    margin: 0;
    color: white;
}

#container {
    height: 100%;
    padding: 2rem;
    text-align: center;
    font-family: sans-serif;
    background-size: cover;
    overflow: auto;
}


input {
    background: none;
    border: none;
    border-bottom: .05rem solid white;
    -webkit-transition: border-bottom .2s ease-in;
    -o-transition: border-bottom .2s ease-in;
    transition: border-bottom .2s ease-in;
    color: white;
    font-size: 1.5rem;
}

input:focus {
    outline: none;
    border-bottom: .15rem solid white;
}

#searchButton {
    cursor: pointer;
    background: none;
    border: none;
    padding: .5rem 2rem;
    border-radius: .5rem;
}

#searchButton:focus {
    outline: none;
}

#searchIcon {
    width: 2rem;
    position: 10px 10px;
}

#searchIcon path {
    -webkit-transform: scale(0.04);
        -ms-transform: scale(0.04);
            transform: scale(0.04);
}

#results {
    background: rgba(0,0,0,.8);
}

#movie {
    text-align: left;
    margin-top: 1em;
}

h1 {
    color: white;
}

h2, h3 {
    color: #58788a;
    font-weight: bold;
}

p {
    color: white;
}

.movie-details-container{
    overflow:auto;
}

.row {
    overflow: auto;
    padding-top: 1em;
    padding-bottom: 1em;
}
.column {
    float: left;
}

.column-sm {
    width: 30%;
}

.column-md {
    width: 50%;
}

.column-lg {
    width: 70%;
}

.column-xlg {
    width: 100%;
}
.badge {
    background-color: #3a4f5b;
    padding:.3rem 1rem;
    border-radius: 4px;
}
#date {
    font-size:1.5rem;
    font-weight: 200;
}
#poster {
    padding-right: 10px;
    margin-left: 80px;
    margin-top: 80px;
    height:400px;
}

#tagline {
    color: #58788a;
}
#genres, #runtime,#rating, #companies, #synopsis{
    color: white;
    margin-right: 80px;
}

#related {
    color: white;
    padding: 0;
}

#related li {
    list-style: none;
    cursor: pointer;
}

#centered-container {
    width: inherit;
    height: 400px;
    text-align: center;
}

#centered-message {
    color: #fff;
    position: relative;
    top: 50%;
    -webkit-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
            transform: translateY(-50%)
}

#centered-message > h1 {
    margin-top: 0;
}

.star-ratings-sprite {
    background: url("https://s3-us-west-2.amazonaws.com/s.cdpn.io/2605/star-rating-sprite.png") repeat-x;
    font-size: 0;
    height: 21px;
    line-height: 0;
    overflow: hidden;
    max-width: 30%;
    text-indent: -999em;
    width: 218px;
}
.star-ratings-sprite-rating {
    background: url("https://s3-us-west-2.amazonaws.com/s.cdpn.io/2605/star-rating-sprite.png") repeat-x;
    background-position: 0 100%;
    float: left;
    height: 21px;
    display: block;
}



